<template>
  <header class="pb-4">
    <img class="profile-pic rounded-circle img-fluid mb-4" src="../assets/jesus.jpg">
    <SocialLinks v-if="enableSocial"/>
    <p class="mb-0">Jesus is a coder by day and bass power chord strummer by night, he also often likes to stay up late going to live rock shows.</p>
  </header>
</template>

<script>
import SocialLinks from './SocialLinks'

export default {
  components: {
    SocialLinks
  },
  data () {
    return {
      enableSocial: true
    }
  }
}
</script>



